import * as PIXI from 'pixi.js'

let Application = PIXI.Application,
    Container = PIXI.Container,
    loader = PIXI.loader,
    resources = PIXI.loader.resources,
    Graphics = PIXI.Graphics,
    Sprite = PIXI.Sprite,
    Text = PIXI.Text,
    TextStyle = PIXI.TextStyle;

let app = '',
    // 史莱姆配置项
    blobInfo = {
        amount: 8,
        spacing: 32,
        xOffset: 150,
        speedy: 3,
        directiony: 1
    },
    // 探险者配置项
    explorerInfo = {
        xSpeed: 5,
        ySpeed: 5,
    }

export function initial(){
    if(resources["gameAssets/treasureHunter.json"] !== undefined){
        loader.reset();
    }
    app = new Application({
        width: 512,
        height: 512,
        antialiasing: true,
        transparent: false,
        resolution: 1
    });
    document.getElementsByClassName('pageCanvas')[0].appendChild(app.view); 
    loader.add('gameAssets/treasureHunter.json').load(setup);           
}
function keyboard(keyCode){
    var key = {};
    key.code = keyCode;
    key.isDown = false;
    key.isUp = true;
    key.press = undefined;
    key.release = undefined;

    key.downHandler = function(event) {
        if (event.keyCode === key.code) {
        if (key.isUp && key.press) key.press();
        key.isDown = true;
        key.isUp = false;
        }
        event.preventDefault();
    };

    key.upHandler = function(event) {
        if (event.keyCode === key.code) {
        if (key.isDown && key.release) key.release();
        key.isDown = false;
        key.isUp = true;
        }
        event.preventDefault();
    };

    window.addEventListener(
        "keydown", key.downHandler.bind(key), false
    );
    window.addEventListener(
        "keyup", key.upHandler.bind(key), false
    );
    return key;
}
function setup(){
    let state, explorer, treasure, blobs, dungeon, door, healthBar, message, gameScene, gameOverScene, id;

    function randomInt(min, max) {
        return Math.floor(Math.random() * (max - min + 1)) + min;
    }

    gameScene = new Container();
    app.stage.addChild(gameScene);

    id = resources["gameAssets/treasureHunter.json"].textures;

    dungeon = new Sprite(id["dungeon.png"]);
    gameScene.addChild(dungeon);

    door = new Sprite(id["door.png"]);
    door.position.set(32, 0);
    gameScene.addChild(door);

    explorer = new Sprite(id["explorer.png"]);
    explorer.x = 68;
    explorer.y = gameScene.height / 2 - explorer.height / 2;
    explorer.vx = 0;
    explorer.vy = 0;
    gameScene.addChild(explorer);

    treasure = new Sprite(id["treasure.png"]);
    treasure.x = gameScene.width - treasure.width - 48;
    treasure.y = randomInt(5, app.stage.height - treasure.height - 5);
    gameScene.addChild(treasure);

    blobs = [];
    let info = blobInfo;
    // 创建怪物，info为怪物总体信息，获取于data
    for(let i=0; i< info.amount; i++) {
        let blob = new Sprite(id["blob.png"]);
        let x = info.spacing * i + info.xOffset;
        let y = randomInt(0, app.stage.height - blob.height);

        blob.x = x;
        blob.y = y;

        blob.vy = info.speedy * info.directiony;
        // blob.vx = info.speedx * info.directionx;

        info.directiony *=  -1;
        blobs.push(blob);

        gameScene.addChild(blob);
    }
    let healthMsgStyle = new TextStyle({
        fontFamily: "-apple-system,BlinkMacSystemFont,Segoe UI,Roboto,Oxygen,Ubuntu,Cantarell,Fira Sans,Droid Sans,Helvetica Neue,sans-serif",
        fontSize: 12,
        fill: "red"        
    })
    let healthMsg = new Text("血量",healthMsgStyle);
    healthMsg.x = app.stage.width - 200;
    healthMsg.y = 0;
    gameScene.addChild(healthMsg);

    healthBar = new Container();
    healthBar.position.set(app.stage.width - 170,4);
    gameScene.addChild(healthBar);

    let innerBar = new Graphics();
    innerBar.beginFill(0x000000);
    innerBar.drawRect(0, 0, 128, 8);
    innerBar.endFill();
    healthBar.addChild(innerBar);

    let outerBar = new Graphics();
    outerBar.beginFill(0xFF3300);
    outerBar.drawRect(0, 0, 128, 8);
    outerBar.endFill();
    healthBar.addChild(outerBar);

    healthBar.outer = outerBar;

    gameOverScene = new Container();
    app.stage.addChild(gameOverScene);
    gameOverScene.visible = false;
    let style = new TextStyle({
        fontFamily: "-apple-system,BlinkMacSystemFont,Segoe UI,Roboto,Oxygen,Ubuntu,Cantarell,Fira Sans,Droid Sans,Helvetica Neue,sans-serif",
        fontSize: 64,
        fill: "red"
    });
    message = new Text("菜", style);
    message.x = app.stage.width / 2 - message.width / 2;
    message.y = app.stage.height / 2 - message.height / 2;
    gameOverScene.addChild(message);

    // 键盘按键绑定
    let left = keyboard(37),
        up = keyboard(38),
        right = keyboard(39),
        down = keyboard(40);

    let eInfo = explorerInfo;
    left.press = function() {
        explorer.vx = -eInfo.xSpeed;
        if(right.isDown){
            explorer.vx = 0;
        }
    };
    keyboard(65).press = left.press;
    left.release = function() {
        if(right.isDown){
            explorer.vx = eInfo.xSpeed;
        }else{
            explorer.vx = 0;
        }
    };
    keyboard(65).release = left.release;

    up.press = function() {
        explorer.vy = -eInfo.ySpeed;
        if(down.isDown){
            explorer.vy = 0;
        }
    };
    keyboard(87).press = up.press; 
    up.release = function() {
        if(down.isDown){
            explorer.vy = eInfo.ySpeed;
        }else{
            explorer.vy = 0;
        }
    };
    keyboard(87).release = up.release;

    right.press = function() {
        explorer.vx = eInfo.xSpeed;
        if(left.isDown){
            explorer.vx = 0;
        }
    };
    keyboard(68).press = right.press;
    right.release = function() {
        if(left.isDown){
            explorer.vx = -eInfo.xSpeed;
        }else{
            explorer.vx = 0;
        }
    };
    keyboard(68).release = right.release;

    down.press = function() {
        explorer.vy = eInfo.ySpeed;
        if (up.isDown) {
            explorer.vy = 0;
        }
    };
    keyboard(83).press = down.press;
    down.release = function() {
        if(up.isDown){
            explorer.vy = -eInfo.ySpeed;
        }else{
            explorer.vy = 0;
        }
    };
    keyboard(83).release = down.release;

    state = play;
    app.ticker.add(delta => gameLoop(delta));

    function  gameLoop(delta){
        state(delta);
    }
    function play() {
        explorer.x += explorer.vx;
        explorer.y += explorer.vy;

        contain(explorer, {x: 28, y: 10, width: 488, height: 480});
        //contain(explorer, stage);

        let explorerHit = false;

        blobs.forEach(function(blob) {
            blob.y += blob.vy;
            let blobHitsWall = contain(blob, {x: 28, y: 10, width: 488, height: 480});
            if (blobHitsWall === "top" || blobHitsWall === "bottom") {
                blob.vy *= -1;
            }
            if(hitTestRectangle(explorer, blob)) {
                explorerHit = true;
            }
        });
        if(explorerHit) {
            explorer.alpha = 0.5;
            healthBar.outer.width -= 30;
        } else {
            explorer.alpha = 1;
        }
        if (hitTestRectangle(explorer, treasure)) {
            treasure.x = explorer.x + 8;
            treasure.y = explorer.y + 8;
        }
        if (healthBar.outer.width < 0) {
            state = end;
            message.text = "菜";
        }
        if (hitTestRectangle(treasure, door)) {
            state = end;
            message.text = "牛！";
        } 
    }
    // 结束页面展示
    function end() {
        gameScene.visible = false;
        gameOverScene.visible = true;
        setTimeout(()=>{
            healthBar.outer.width = 128;
            explorer.x = 68;
            explorer.y = gameScene.height / 2 - explorer.height / 2;
            explorer.vx = 0;
            explorer.vy = 0;
            gameOverScene.visible = false;
            gameScene.visible = true;
            state = play
        },3000);
    }
    function contain(sprite, container) {
        let collision;

        if (sprite.x < container.x) {
            sprite.x = container.x;
            collision = "left";
        }

        if (sprite.y < container.y) {
            sprite.y = container.y;
            collision = "top";
        }

        if (sprite.x + sprite.width > container.width) {
            sprite.x = container.width - sprite.width;
            collision = "right";
        }

        if (sprite.y + sprite.height > container.height) {
            sprite.y = container.height - sprite.height;
            collision = "bottom";
        }
        return collision;
    }
    // 碰撞检测
    function hitTestRectangle(r1, r2) {

        let hit, combinedHalfWidths, combinedHalfHeights, vx, vy;
        hit = false;

        r1.centerX = r1.x + r1.width / 2; 
        r1.centerY = r1.y + r1.height / 2; 
        r2.centerX = r2.x + r2.width / 2; 
        r2.centerY = r2.y + r2.height / 2; 

        r1.halfWidth = r1.width / 2;
        r1.halfHeight = r1.height / 2;
        r2.halfWidth = r2.width / 2;
        r2.halfHeight = r2.height / 2;

        vx = r1.centerX - r2.centerX;
        vy = r1.centerY - r2.centerY;

        combinedHalfWidths = r1.halfWidth + r2.halfWidth;
        combinedHalfHeights = r1.halfHeight + r2.halfHeight;

        if (Math.abs(vx) < combinedHalfWidths) {
            if (Math.abs(vy) < combinedHalfHeights) {
            hit = true;
            } else {
            hit = false;
            }
        } else {
            hit = false;
        }
        return hit;
    }
}