<template>
  <div id="app2">
    <header>
      <div class="nav">
        <el-menu class="el-menu-demo" mode="horizontal" @select="handleSelect" 
        :background-color="color.background" :text-color="color.text" default-active="2" :active-text-color="color.active">
            <el-menu-item index="1">首页</el-menu-item>
            <el-menu-item index="2">BLOG</el-menu-item>
            <el-menu-item index="3">关于</el-menu-item>
        </el-menu>
      </div>
      <div class="downline"></div>
    </header>
    <article>
      <section>
          <router-view><!-- 此处为内容渲染处 --></router-view>
      </section>
    <footer>
      <div class="upperLine"></div>
      <div class="webMsg">
      <!-- 网页底部信息 -->
        <p>powerBy&nbsp;:&nbsp;kokodayo<br/></p>
        <p>&nbsp;&nbsp;buildBy&nbsp;:&nbsp;Vue</p>
      </div>
    </footer>
    </article>
    <footer>
      <div class="Canvas" style="position: fixed; right: 10px; bottom: 10px;z-index: 99999999" id="L2dCanvas"></div>
    </footer>
  </div>
</template>

<script>
export default {
  name: 'mainStructure',
  data(){
    return{
      color: {
        background: '#ffffff',
        text: '#303133',
        active: '#409EFF'
      },
    }
  },
  created(){
  setTimeout(() => {
          new window.l2dViewer({
              el: document.getElementById('L2dCanvas'),
              basePath: 'https://cdn.jsdelivr.net/npm/shuangdm@latest/assets',
              modelName: '233重制版2',
              sizeLimit: false,
              mobileLimit: false,
              height: 1200,
              // biaoqiang_3 双刀妹（团子）双刀妹 233重制版2
              // https://cdn.jsdelivr.net/npm/live2dv3@latest/assets https://cdn.jsdelivr.net/npm/shuangdm@latest/assets
              // sounds: [
              //     'sounds/demo.mp3',
              //     'https://cdn.jsdelivr.net/npm/live2dv3@latest/assets/biaoqiang_3/sounds/demo.mp3'
              // ]
          })
      },1000)
  },
  methods:{
    handleSelect(key){
      if(key === '1')
      this.$router.push({path: '/welcome'});
      if(key === '2')
      this.$router.push({path: '/blog'});
      if(key === '3')
      this.$router.push({path: '/about'});
    },
  }
}
</script>

<style lang="less" scoped>
#app2{
  height: 100vh;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  header{
    width: 100%;
    height: 60px;
    display: flex;
    flex-direction: column;
    margin-bottom: 2px;
    .nav{
      display: flex;
      justify-content: space-evenly;
      flex-grow: 1;
      width: 100%;
    }
    .downline{
      width: 70%;
      padding-top: 1px;
      height: 1px;
      background-color: #dcdfe6;
      margin-right: auto;
      margin-left: auto;
    }
  }
  article{
    overflow-y: auto;
    display: flex;
    flex-direction: column;
    // background-color: black;
    section{
      width: 80%;
      min-height: 90vh;
      flex-grow: 1;
      margin-right: auto;
      margin-left: auto;
      background-color: transparent;
    }
    footer{
      width: 100%;
      height: 80px;
      margin-right: auto;
      margin-left: auto;
      padding-top: 10px;
      padding-bottom: 10px;
      color: #d0d3da;
      background-color: rgba(256, 256, 256, .1);
      font-size: 10px;
      .upperLine{
        width: 50%;
        height: 1px;
        margin-left: auto;margin-right: auto;
        margin-bottom: 5px;
        background-color: #dcdfe6;
      }
      .webMsg{
        width: 120px;
        margin-right: auto;
        margin-left: auto;
        line-height: 15px;
      }
    }    
  }

}
</style>
