<template>
    <div class="maincontainer">
        <header>
            <div class="nav">
                <el-menu class="el-menu-demo" mode="horizontal" @select="handleSelect" 
                :background-color="color.background" :text-color="color.text" default-active="3" :active-text-color="color.active">
                    <el-menu-item index="1">首页</el-menu-item>
                    <el-menu-item index="2">BLOG</el-menu-item>
                    <el-menu-item index="3">关于</el-menu-item>
                </el-menu>
            </div>
            <div class="downline"></div>
        </header>
        <div class="msg" style="text-align: center;margin-top: 15px">这里什么都没有，就先放个pixi写的小游戏吧</div>
        <div ref='pageCanvas' class='pageCanvas'></div>
    </div>
</template>
<script>
import * as PIXI from 'pixi.js'
import { initial } from "../service/game/treasureHunter.js"
export default {
    name: 'about',
    data(){
        return{
        color: {
            background: '#ffffff',
            text: '#303133',
            active: '#409EFF'
        },
        }
    },
    methods:{
        handleSelect(key){
        PIXI.utils.clearTextureCache();
        if(key === '1')
        this.$router.push({path: '/welcome'});
        if(key === '2')
        this.$router.push({path: '/blog'});
        if(key === '3')
        this.$router.push({path: '/about'});
        },
    },
    mounted(){
        initial();
    },
}
</script>
<style lang='less' scoped>
.maincontainer{
    header{
        width: 100%;
        height: 60px;
        display: flex;
        flex-direction: column;
        margin-bottom: 2px;
        .nav{
        display: flex;
        justify-content: space-evenly;
        flex-grow: 1;
        width: 100%;
        }
        .downline{
        width: 70%;
        padding-top: 1px;
        height: 1px;
        background-color: #dcdfe6;
        margin-right: auto;
        margin-left: auto;
        }
    }
    .pageCanvas{
        width: 512px;
        margin-left: auto;margin-right: auto;
        margin-top: 60px;
    }
}
</style>